/* Nav-toggle */

.nav-toggle {
    width: 30px;
    padding: 3px 0;
    display: none;

    font-size: 0;
    color: transparent;

    border: 0;
    background: none;
    cursor: pointer;

    position: absolute;
    top: 24px;
    right: 24px;
    z-index: 2; }


.nav-toggle:focus {
    outline: 0; }


.nav-toggle__item {
    display: block;
    width: 100%;
    height: 3px;

    background-color: #fff;

    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;

    transition: background .2s linear; }


.nav-toggle.active .nav-toggle__item {
    background: none; }


.nav-toggle__item:before,
.nav-toggle__item:after {
    content: "";
    width: 100%;
    height: 3px;

    background-color: #fff;

    position: absolute;
    left: 0;
    z-index: 1;

    transition: transform .2s linear; }


.nav-toggle__item:before {
    top: -8px; }


.nav-toggle__item:after {
    bottom: -8px; }


.nav-toggle.active .nav-toggle__item:before {
    transform-origin: left top;
    transform: rotate(45deg) translate3d(0px, -2px, 0); }


.nav-toggle.active .nav-toggle__item:after {
    transform-origin: left bottom;
    transform: rotate(-45deg) translate3d(0px, 3px, 0); }



@media ($tablet) {

 // Nav
	.nav {
		display: none;
		width: 100%;

		position: absolute;
		top: 100%;
		left: 0;

		background-color: #000; }

	.nav.active {
		display: block; }

	.nav__link {
        display: block;
        margin: 0;
        padding: 10px 20px;
        text-align: center; }

	.nav-toggle {
		display: block; } }




